@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;600&family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body{
  width: 100%;
  margin: 0;
  padding:0;
  overflow-x: hidden; 
}
.App {
  text-align: center;
}
p {
  text-align: left;
}
h3 {
  margin-bottom: 5px;
  color: #ebebeb;
}
/* font-family: 'Poppins', sans-serif;
font-family: 'Roboto', sans-serif; */

h1, h2, h3 {
  font-family: 'Poppins', sans-serif;
}
p, form, li, button, a{
  font-family: 'Roboto', sans-serif;
}



.header_container {
    background: #000099;
    display: flex;
    margin: auto;
    justify-content: space-between;
  }


header a, .header_title {
    text-decoration: none;
    color:#ffffff;
}

.header_title {
    font-size: 26px;
    margin: 0px 0px 0px 20px;
}

.header_nav {
    margin:0 10px;
}



.nav_open{
    margin: 5px 10px;
}

.nav_open {
        display: flex;
    }
    .nav_links {
        margin: 0 10px 0 10px;
    }

@media all and (min-width:551px) {
    .contact_link, .nav_hidden{
        display: contents;
    }

    
}

